.spon-logos {
  /* width: 100%; */
  height: 600px;
  border-radius: 1rem;
  margin: 1rem 0;
  transition: 0.4s linear;
}

.spon-logos:hover {
  transform: scale(1.1);
  transition: 0.4s linear;
}

.sponsors h2 {
  color: #fff;
}

.header3 {
  position: relative;
  overflow: hidden;
}

.header3::before {
  position: absolute;
  top: 50%;
  left: 130%;
  overflow: hidden;
  width: 70rem;
  height: 3px;
  content: "\a0";
  background: linear-gradient(-45deg, #ee7752, #e73c7e, #23a6d5, #23d5ab);
  background-size: 200% 200%;
  animation: gradient 5s ease infinite;
}

@media only screen and (max-width: 1200px) {
  .header3 {
    text-align: center !important;
  }
  .header3::before {
    display: none;
  }
}

@media only screen and (max-width: 786px) {
  .spon-logos {
    height: 150px;
  }
}
